import React, { Component } from "react";
import { Link } from "react-router-dom";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { logoutUser } from "../../actions/authActions";

class Navbar extends Component {
  onLogoutClick(e) {
    e.preventDefault();
    //this.props.clearCurrentProfile();
    // this.props.history.push('/Landing');
    this.props.logoutUser();
  }

  render() {
    const { isAuthenticated, user } = this.props.auth;

    const authlinks = (
      <ul className="navbar-nav">
        <li className="nav-item dropdown">
          <Link
            className="nav-link dropdown-toggle"
            to="#"
            id="navbarDropdownMenuLink1"
            role="button"
            data-toggle="dropdown"
            aria-haspopup="false"
            aria-expanded="true"
          >
            Home
          </Link>
          <div
            className="dropdown-menu"
            aria-labelledby="navbarDropdownMenuLink1"
          >
            <Link className="dropdown-item" to="/About">
              About Research
            </Link>

            <Link className="dropdown-item" to="/Participation">
              Participation
            </Link>
            <Link className="dropdown-item" to="/TetCs">
              T&amp;Cs
            </Link>
            <Link className="dropdown-item" to="/Contacts">
              Contacts
            </Link>
          </div>
        </li>

        <li className="nav-item dropdown">
          <Link
            className="nav-link dropdown-toggle"
            href="#"
            id="navbarDropdownMenuLink2"
            role="button"
            data-toggle="dropdown"
            aria-haspopup="true"
            aria-expanded="false"
          >
            Participants
          </Link>
          <div
            className="dropdown-menu"
            aria-labelledby="navbarDropdownMenuLink2"
          >
            <Link className="dropdown-item" to="/Register">
              Registration Page
            </Link>
            <Link className="dropdown-item" to="/Information">
              Information Page
            </Link>
            <Link className="dropdown-item" to="/CRanking">
              Copper Ranking
            </Link>
            <Link className="dropdown-item" to="/GRanking">
              Gold Ranking
            </Link>
            <Link className="dropdown-item" to="/RMethod">
              Ranking Method
            </Link>
            <Link className="dropdown-item" to="/MonthPrices">
              Copper/Gold Prices input
            </Link>
          </div>
        </li>

        <li className="nav-item dropdown">
          <Link
            className="nav-link dropdown-toggle"
            href="#"
            id="navbarDropdownMenuLink2"
            role="button"
            data-toggle="dropdown"
            aria-haspopup="true"
            aria-expanded="false"
          >
            Inputs
          </Link>
          <div
            className="dropdown-menu"
            aria-labelledby="navbarDropdownMenuLink2"
          >
            <Link className="dropdown-item" to="/Copperinput">
              Copper Input
            </Link>
            <Link className="dropdown-item" to="/Goldinput">
              Gold Input
            </Link>
          </div>
        </li>

        <li className="nav-item dropdown">
          <Link
            className="nav-link dropdown-toggle"
            href="#"
            id="navbarDropdownMenuLink2"
            role="button"
            data-toggle="dropdown"
            aria-haspopup="true"
            aria-expanded="false"
          >
            Forecasts
          </Link>
          <div
            className="dropdown-menu"
            aria-labelledby="navbarDropdownMenuLink2"
          >
            <Link className="dropdown-item" to="/CForecast">
              Copper Forecasts
            </Link>
            <Link className="dropdown-item" to="/GForecast">
              Gold Forecasts
            </Link>
          </div>
        </li>

        <li className="nav-item dropdown">
          <Link
            className="nav-link dropdown-toggle"
            href="#"
            id="navbarDropdownMenuLink3"
            role="button"
            data-toggle="dropdown"
            aria-haspopup="true"
            aria-expanded="false"
          >
            Data
          </Link>
          <div
            className="dropdown-menu"
            aria-labelledby="navbarDropdownMenuLink3"
          >
            <Link className="dropdown-item" to="/CData">
              Copper Data
            </Link>
            <Link className="dropdown-item" to="/RetardKbp">
              Gold Data
            </Link>
          </div>
        </li>

        <li className="nav-item dropdown">
          <Link
            className="nav-link dropdown-toggle"
            href="#"
            id="navbarDropdownMenuLink2"
            role="button"
            data-toggle="dropdown"
            aria-haspopup="true"
            aria-expanded="false"
          >
            Documents archive
          </Link>
          <div
            className="dropdown-menu"
            aria-labelledby="navbarDropdownMenuLink2"
          >
            <Link className="dropdown-item" to="/CForecast">
              Copper
            </Link>
            <Link className="dropdown-item" to="/GForecast">
              Gold
            </Link>
          </div>
        </li>

        <li className="nav-item button-toggle">
          <button
            className="btn btn-outline-secondary my-2 my-sm-0"
            onClick={this.onLogoutClick.bind(this)}
          >
            Logout
          </button>
        </li>
      </ul>
    );

    const nonauthlinks = <div></div>;

    return (
      <nav className="navbar navbar-expand-sm navbar-dark bg-dark mb-4">
        <Link className="navnav-link" to="/">
          COMMODITY FORECASTS
        </Link>

        <button
          className="navbar-toggler"
          type="button"
          data-toggle="collapse"
          data-target="#navbarNavDropdown"
          aria-controls="navbarNavDropdown"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>

        <div className="container">
          <div className="navbar-collapse" id="navbarNavAltMarkup">
            <div className="navbar-nav">
              {isAuthenticated ? authlinks : nonauthlinks}
            </div>
          </div>
        </div>
      </nav>
    );
  }
}

Navbar.propTypes = {
  logoutUser: PropTypes.func.isRequired,
  auth: PropTypes.object.isRequired,
};

const mapStateToProps = (state) => ({
  auth: state.auth,
});

export default connect(mapStateToProps, { logoutUser })(Navbar);
