import React, { Component } from "react";
import PropTypes from "prop-types";
import { withRouter } from "react-router-dom";
import { connect } from "react-redux";
import { registerUser } from "../../actions/authActions";

import TextFieldGroup from "../common/TextFieldGroup";

class Register extends Component {
  constructor() {
    super();
    this.state = {
      name: "",
      password: "",
      password2: "",
      email: "",
      altemail: "",
      realname: "",
      commodity: "",
      sex: "",
      age: "",
      geolocation: "",
      background: "",
      workplace: "",
      forecastingapproach: "",
      bio: "",

      errors: {},
    };

    this.onChange = this.onChange.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
  }

  componentDidMount() {
    if (this.props.auth.isAuthenticated) {
      //  this.props.history.push("/dashboard");
    }
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.errors) {
      this.setState({ errors: nextProps.errors });
    }
  }

  onChange(e) {
    this.setState({ [e.target.name]: e.target.value });
  }

  onSubmit(e) {
    e.preventDefault();

    const newUser = {
      name: this.state.name,
      password: this.state.password,
      password2: this.state.password2,
      email: this.state.email,
      altemail: this.state.altemail,
      realname: this.state.realname,
      commodity: this.state.commodity,
      sex: this.state.sex,
      age: this.state.age,
      geolocation: this.state.geolocation,
      professionBackground: this.state.professionBackground,
      workplace: this.state.workplace,
      forecastingapproach: this.state.forecastingapproach,
      bio: this.state.bio,
    };

    this.props.registerUser(newUser, this.props.history);
  }

  render() {
    const { errors } = this.state;
    const { user } = this.props.auth;
    //equivalent 
    //const user = this.props.auth.user;

    return (
      <div class="register">
        {/*user ? user.name : null*/}

        <div className="container">
          <div className="row">
            <div className="col-md-8 m-auto">
              <h1 className="display-4 text-center">Sign Up</h1>
              <p className="lead text-center">Create your account</p>
              <form noValidate onSubmit={this.onSubmit}>
                <TextFieldGroup
                  placeholder="Name"
                  name="name"
                  value={this.state.name}
                  onChange={this.onChange}
                  error={errors.name}
                />

                <TextFieldGroup
                  placeholder="Password"
                  name="password"
                  type="password"
                  value={this.state.password}
                  onChange={this.onChange}
                  error={errors.password}
                />
                <TextFieldGroup
                  placeholder="Confirm Password"
                  name="password2"
                  type="password"
                  value={this.state.password2}
                  onChange={this.onChange}
                  error={errors.password2}
                />

                <TextFieldGroup
                  placeholder="Email"
                  name="email"
                  type="email"
                  value={this.state.email}
                  onChange={this.onChange}
                  error={errors.email}
                  info="This site uses Gravatar so if you want a profile image, use a Gravatar email"
                />

                <TextFieldGroup
                  placeholder="Alternative email (Optional)"
                  name="altemail"
                  type="email"
                  value={this.state.altemail}
                  onChange={this.onChange}
                  error={errors.altemail}
                />

                <TextFieldGroup
                  placeholder="Real Name (For official communication only)"
                  name="realname"
                  type="text"
                  value={this.state.realname}
                  onChange={this.onChange}
                  error={errors.realname}
                />

                <div className="form-group">
                  <select
                    name="commodity"
                    value={this.state.commodity}
                    onChange={this.onChange}
                    error={errors.commodity}
                    className="form-control form-control-lg"
                  >
                    <option value="">--Commodity forecasting--</option>
                    <option value="Copper">Copper</option>
                    <option value="Gold">Gold</option>
                  </select>
                </div>

                <div className="form-group">
                  <select
                    name="sex"
                    value={this.state.sex}
                    onChange={this.onChange}
                    error={errors.sex}
                    className="form-control form-control-lg"
                  >
                    <option value="">--Sex (Optional)--</option>
                    <option value="Male">Male</option>
                    <option value="Female">Female</option>
                    <option value="Blank">Blank</option>
                  </select>
                </div>

                <div className="form-group">
                  <select
                    name="age"
                    value={this.state.age}
                    onChange={this.onChange}
                    error={errors.age}
                    className="form-control form-control-lg"
                  >
                    <option value="">--Age (Years) --</option>
                    <option value="<25">&lt; 25 </option>
                    <option value="25-35">25-35</option>
                    <option value="35-45">35-45</option>
                    <option value="45-55">45-55</option>
                    <option value="55-65">55-65</option>
                    <option value=">65">&gt;65</option>
                  </select>
                </div>

                <div className="form-group">
                  <select
                    name="geolocation"
                    value={this.state.geolocation}
                    onChange={this.onChange}
                    error={errors.geolocation}
                    className="form-control form-control-lg"
                  >
                    <option value="">--Geographic location --</option>
                    <option value="Africa">Africa</option>
                    <option value="Asia">Asia</option>
                    <option value="Australia">Australia</option>
                    <option value="Europe">North America</option>
                    <option value="South America">Sout America</option>
                  </select>
                </div>

                <div className="form-group">
                  <select
                    name="professionBackground"
                    value={this.state.professionBackground}
                    onChange={this.onChange}
                    error={errors.professionBackground}
                    className="form-control form-control-lg"
                  >
                    <option value="">--Professional Background --</option>
                    <option value="Financial">Financial</option>
                    <option value="HR">HR</option>
                    <option value="Journalism">Journalism</option>
                    <option value="Legal">Legal</option>
                    <option value="Technical">Technical</option>
                    <option value="Other">Other</option>
                  </select>
                </div>

                <div className="form-group">
                  <select
                    name="workplace"
                    value={this.state.workplace}
                    onChange={this.onChange}
                    error={errors.workplace}
                    className="form-control form-control-lg"
                  >
                    <option value="">--Workplace--</option>
                    <option value="Academic">Academic</option>
                    <option value="Consultancy">Consultancy</option>
                    <option value="Financial institution">
                      Financial institution
                    </option>
                    <option value="Mining organization">
                      Mining organization
                    </option>
                    <option value="Technical services">
                      Technical services
                    </option>
                    <option value="Research organization">
                      Research organization
                    </option>
                  </select>
                </div>

                <div className="form-group">
                  <select
                    name="forecastingapproach"
                    value={this.state.forecastingapproach}
                    onChange={this.onChange}
                    error={errors.forecastingapproach}
                    className="form-control form-control-lg"
                  >
                    <option value="">--Forecasting approach--</option>
                    <option value="Econometric modeling">
                      Econometric modeling
                    </option>
                    <option value="Fundamental analysis">
                      Fundamental analysis
                    </option>
                    <option value="Futures prices">Futures prices</option>
                    <option value="Historical averages">
                      Historical averages
                    </option>
                    <option value="Research organization">
                      Research organization
                    </option>
                    <option value="Technical analysis">
                      Technical analysis
                    </option>
                  </select>
                </div>

                <div className="form-group">
                  <textarea
                    placeholder="Short Bio"
                    name="bio"
                    type="textarea"
                    value={this.state.bio}
                    onChange={this.onChange}
                    error={errors.bio}
                    className="form-control form-control-lg"
                  />
                </div>

                <div className="form-group">
                  <table className="table table-bordered">
                    <thead>
                      <tr>
                        <td colspan="2">PARTICIPANT CONSENT TERMS</td>
                      </tr>
                      <tr>
                        <td>Consent questions</td>
                        <td>yes/no</td>
                      </tr>
                    </thead>
                    <tbody>
                      <tr>
                        <td>
                          I confirm that I have read and have understood the
                          Participant Information Page for this study. I have
                          had the opportunity to consider the information, ask
                          questions, and have had these answered satisfactorily.
                        </td>
                        <td>
                          <div className="radio">
                            <label>
                              <input
                                type="radio"
                                name="question1"
                                id="y1"
                                value="yes"
                              />
                              yes
                            </label>
                          </div>
                          <div class="answ">
                            <label for="n1">
                              <input
                                type="radio"
                                name="question1"
                                id="n1"
                                value="no"
                              />
                              no
                            </label>
                          </div>
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>

                <input type="submit" className="btn btn-info btn-block mt-4" />
              </form>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

Register.prototypes = {
  registerUser: PropTypes.func.isRequired,
  auth: PropTypes.object.isRequired,
};

const mapStateToProps = (state) => ({
  auth: state.auth,
});

export default connect(mapStateToProps, { registerUser })(Register);
